import Lines from '@/components/common/Lines.vue'
export default {
	data() {
		return {
			//输入的关键词
			keyword:'',
			//搜索过的词记录
			searchData:[]
		}
	},
	//页面加载的时候
	onLoad(){
		uni.getStorage({
			key:"searchData",
			success: (res) => {
				this.searchData = JSON.parse( res.data );
			}
		})
	},
	//监听input输入内容
	onNavigationBarSearchInputChanged(e){
	     this.keyword = e.text;
	},
	//点击顶栏中的搜索按钮
	onNavigationBarButtonTap(e){
		this.search();	
	},
	//监听软键盘的搜索按钮点击的
	onNavigationBarSearchInputConfirmed(){
		this.search();	
	},
	components:{
		Lines
	},
	methods: {
		//判断关键词是否为空和跳转页面的
		search(){
			if( this.keyword ==="" ){
				return uni.showToast({
					title:"关键词不能为空",
					icon:"none"
				})
			}else{
				this.toSearchList(this.keyword);
			}
			uni.hideKeyboard();
			this.addSearch();
		},
		//记录最近搜索词
		addSearch(){
			
			let idx = this.searchData.indexOf(this.keyword);
			if( idx < 0 ){
				this.searchData.unshift(  this.keyword );
			}else{
				this.searchData.unshift( this.searchData.splice(idx,1)[0] );
			}
			
			uni.setStorage({
				key:"searchData",
				data: JSON.stringify(this.searchData)
			})
		},
		//清除搜索记录
		clearHistory(){
			uni.showModal({
				title:"重要提示",
				content:'是否要清除搜索记录',
				cancelText:'取消',
				confirmText:"确定",
				success: (res) => {
					if(res.confirm){
						uni.removeStorage({
							key:"searchData"
						})
						this.searchData=[];
					}
				}
			})
		},
		//点击搜索记录进入页面
		toSearchList(item){
			uni.navigateTo({
				url:"../search-list/search-list?keyword="+item+""
			})
		}
	}
}
